@include xl-block { 
    .footer {
        display: flex;
        flex-direction: column;
    }
}
.footer {
    .footer_xl {
        @include sm(display, block);
        @include xs(display, none);

        @include sm-block {

            .footer__navigation {
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-bottom: 20px;
                margin-top: 120px;
                width: 100%;

                .footer__navigation_link {
                    order: 2;
                    align-self: flex-start;
                    ul {    
                        display: flex;
                        flex-direction: row;
                        justify-content: flex-start;
                        flex-wrap: nowrap; 

                        li{
                            
                            margin-right: 10px;
                            a {
                                font-size: 20px;
                                font-weight: 300;
                        
                              }
                            }   
                        }

                }

                .footer__socials {
                    display: flex;
                    flex-wrap: nowrap;
                    justify-content: space-around;
                    order: 1;
                    margin-bottom: 40px;
                    width: 200px;
                    height: 60px;
                    img {
                        width: 60px;
                        height: auto;
                    }
                }
            }
            .footer_copyright {
                    font-size: 12px;
                    margin-bottom: 30px;
                }
        }

        @include md-block {
            .footer__navigation {
                flex-direction: row;
                justify-content: space-between;
                .footer__navigation_link {
                    align-self: flex-start;
                    order: 1;
                    ul {  
                        align-items: center;  
                        display: flex;
                        flex-direction: row;
                        justify-content: flex-start;
                        flex-wrap: nowrap;  
                        li{
                            margin-right: 10px;
                            a {
                                font-size: 20px;
                                font-weight: 300;
                                line-height: 36px;
                              }
                            }   
                        }
                }

                .footer__socials {
                    display: flex;
                    flex-wrap: nowrap;
                    justify-content: space-around;
                    margin-bottom: 0px;
                    width: 100px;
                    height: 60px;
                    order: 2;
                    img {
                        width: 36px;
                        height: auto;
                    }
                }
            }
            .footer_copyright {
                    font-size: 12px;
                    margin-bottom: 30px;
                }
        }
        
        @include lg-block {
            position: absolute;
            bottom: 0px;
            width: 100%;
            .footer__navigation {
                .footer__navigation_link {
                    ul {  
                        li{
                            a {
                                font-size: 16px;
                              }
                            }   
                        }
                }

                .footer__socials {
                    width: 140px;
                }
            }
        }

        @include xl-block {
       
            .footer__navigation {
                margin-top: 80px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
             
                margin-bottom: 20px;
            }
        
            .footer__navigation_link ul {
                display: flex;
                flex-direction: row;
                align-self: center ;

                li:first-child {
                    padding-left: 0;
                }
                
                li a{
                    font-size: 18px;
                    word-wrap: normal;
                    font-weight: 300;
                }
            }
        
            .footer__socials {
                display: flex;
                justify-content: space-between;
                height: 36px;
                max-width: 210px;
         
                a {
                    padding: 0;
                }
            }
        
            .footer_copyright {
                font-size: 14px;
            }
        }
    }

    .footer_xs {
        @include sm(display, none);
        @include size-xs(13);
        @include xs-block {
            margin-top: 60px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            margin-left: -15px;

            .nav {
                height: 400px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                text-align: center;
                font-size: 28px;
                font-weight: 700;
                margin-bottom: 60px;
            }

            .socials {
                display: flex;
                justify-content: space-between;
                width: 185px;
                margin: 0 auto 80px;
                img {
                    width: 60px;
                    height: auto;                    
                }
            }
            
            .footer_text {
                display: flex;
                justify-content: space-between;
                width: 100%;
                margin-bottom: 30px;
                li {
                    a{
                        font-size: 14px;
                    }
                }
            }

            .footer_copyright {
                text-align: center;
                font-size: 12px;
                margin-bottom: 30px;
            }
        }
    }
}




