  $step_lg: 71px;
  $way_lg: 352px;

  @keyframes move_chat_lg {
    0% {
      transform: translateY($way_lg);
    }
    5% {
      transform: translateY($way_lg - $step_lg);
    }
    15% {
      transform: translateY($way_lg - $step_lg);
    }
    20% {
      transform: translateY($way_lg - 2 * $step_lg);
    }
    30% {
      transform: translateY($way_lg - 2 * $step_lg);
    }
    35% {
      transform: translateY($way_lg - 3 * $step_lg);
    }
    45% {
      transform: translateY($way_lg - 3 * $step_lg);
    }
    50% {
      transform: translateY($way_lg - 4 * $step_lg);
    }
    60% {
      transform: translateY($way_lg - 4 * $step_lg);
    }
    65% {
      transform: translateY($way_lg - 5 * $step_lg);
    }
    75% {
      transform: translateY($way_lg - 5 * $step_lg);
    }
    100% {
      transform: translateY($way_lg - 5 * $step_lg);
    }
  }



  $step: 81px;
  $way: 402px;

@keyframes move_chat {
  0% {
    transform: translateY($way);
  }
  5% {
    transform: translateY($way - $step);
  }
  15% {
    transform: translateY($way - $step);
  }
  20% {
    transform: translateY($way - 2 * $step);
  }
  30% {
    transform: translateY($way - 2 * $step);
  }
  35% {
    transform: translateY($way - 3 * $step);
  }
  45% {
    transform: translateY($way - 3 * $step);
  }
  50% {
    transform: translateY($way - 4 * $step);
  }
  60% {
    transform: translateY($way - 4 * $step);
  }
  65% {
    transform: translateY($way - 5 * $step);
  }
  75% {
    transform: translateY($way - 5 * $step);
  }
  100% {
    transform: translateY($way - 5 * $step);
  }
}

