.doc {
  @include section();
  flex-direction: column;
  width: 1140px;
  margin-bottom: 120px;

  .doc__title {
    @include xs-block {
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin-bottom: 30px;
    }
    @include xl-block {
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 60px;
      margin-top: 71px;
    }
    position: relative;
      display: flex;
 

      h1 {
       font-size: 48px;
       
       @include xl-block{
        text-align: left;
       }
       @include xs-block {
         order: 1;
       }
      }


      .comment { 
        @include xs-block {
          order: 2;
          position: relative;
          top: 0;
          font-size: 16px;
          line-height: 34px;
          margin: 0;
        }
        @include xl-block {
          position: absolute;
          top: 70px;
          font-size: 22px;
          left: 2px;
        }
        font-size: 22px;
        line-height: 34px;
        color: #9297A2;
        font-weight: 300;
        
      }

  }

  .cookie_title {
    display: flex;
    flex-direction: column;

    @include xs-block {

      align-items: center;
    }
    @include xl-block {
      margin-top: 71px ;
      align-items: flex-start;
    }

    h1 {
      font-size: 48px;
      @include xs-block {
        text-align: center;
        margin-bottom: 20px;
        // order: 1;
      }
      @include xl-block {
        text-align: left;
        line-height: 54px;
      }
     }
     .comment { 
      @include xs-block {
        order: 2;
        font-size: 16px;
        line-height: 34px;
        margin: 0;
      }
      @include xl-block {
        font-size: 22px;
        line-height: 34px;
        text-align: left;
        margin-bottom: 20px;
      }
      font-size: 22px;
      line-height: 34px;
      color: #9297A2;
      font-weight: 300;
      
    }
     

  }
  


  .doc__text {
    @include xs-block {
    }
    font-size: 18px;
    line-height: 24px;
    font-weight: 300;
  }

}

.xs {
  @include xs(display, block);
}

.xs_n { 
  @include xs(display, none);
}

