.cookie {
    position: fixed;
    bottom: 0;
    height: 14.8vh;
    width: 100vw;
    background: #FFFFFF;
}




.cookie_content {
    height: 100%;
    display: flex;
    flex-direction: row;
    width: 1244px;
    margin: 0 auto;
    align-items: center;

    .text {

        width: 100%;
        // height: 100%;
        color: #000;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: -0.337647px;
        margin: 0;

        h4{
            font-weight: 400;
        }

        a {
            color: #0072FF;
            &:hover {
                text-decoration: underline;
            }     
        }

        p {
            margin: 0;
        }
        
        
    }
    .cookie_toggle {
        position: relative;
        top: 5px;
        cursor: pointer;
    }
}



