@mixin feach_icon {
    position: absolute;
    left: -3px;
    top: -10px;
    display: inline-block;
    background: red;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    color: white;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
}

@mixin description {
    width: 50%;
    display: flex;
    flex-direction: column;
}

@mixin section {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
}

@mixin action_item {
    display: inline-block;
    width: 50px;
    height: 50px;
    color: #fff;
    background: #000;
    font-weight: bold;
    font-size: 26px;
    border-radius: 25px;
    line-height: 50px;
    text-align: center;
    position: absolute;
    left: 27px;
    top: 25px;
}

@mixin hyvw__item_icon {
    display: inline-block;
    box-sizing: border-box;
    position: absolute;
    left: 0;
    font-size: 40px;
    width: 90px;
    height: 90px;
    border: 10px solid #F2F2F2;
    border-radius: 28px;
    line-height: 70px;
    text-align: center;
    @include xs-block {
        top: -5px;
    }
    @include xl-block {
        top: -27px;
    }
}

@mixin container-full_doc() {
    padding-left: 0;
    padding-right: 0;
}

@mixin container_doc() {
    max-width: 1280px;
    margin: 0 auto;
    @include container-full_doc();
}

@mixin spiner_typing {
    p {
        overflow-x: hidden;
        white-space: nowrap;
        span {
            display: inline-block;
            width: 2px;
            height: 2px;
            border-radius: 10px;
            background: #fff;
            @for $i from 1 through 3 {
                &:nth-child(#{$i}) {
                  animation: 1.5s typing_spiner infinite cubic-bezier(0.550, 0.085, 0.680, 0.530) ($i * .3333s);
                }
        }
    }
}   
}

@mixin chat_message {
    margin-top: 30px;
    padding: 10px;
    border-radius: 17.75px;
    display: inline-block; 
   }

   @mixin typing {
    @include chat_message();
    @include spiner_typing();
    font-weight: 300;
    position: absolute;
    width: 90px;
    color: #fff;
    opacity: 0;
}