.header {
    padding-top: 10vh;
    padding-bottom: 52px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 4;
    @include xs-block {
        padding-top: 62px;
        padding-bottom: 71px;
    }
    @include xl-block {
        padding-top: 80px;
        padding-bottom: 61px;
    }
}
.first_header {
    .logo {
        margin-left: -8px; ;
    }
}

.logo {
    position: relative;
    @include col-ib();
    @include size-xs(6);
    @include size-xl(4);

    img {
        max-width: 100%;
        height: auto;
    }
    sup {
        position: absolute;
        text-transform: uppercase;
        font-weight: 500;
       
        @include xs-block {
            top: 45px;
        }
        @include sm-block {
            top: 65px;
        }
        @include xl-block {
            top: 68px;
        }
    }
}


.lang {
    @include xs(display, none);
    @include lg(display, inherit);
    @include lg-block {
        left: -255px;
    }
    @include xl-block {
       left: -17.5%; 
    }

    position: relative;

    .selected_lang {
        @include xs-block{
            width: 80px;
        }
        @include xl-block{
            width: 80px;
        }
        background: no-repeat url("../img/svg/Arr_for_select.svg");
        background-size: 25%;
        font-weight: 700;
        font-size: 24px;
        width: 60px;
        height: 40px;
        padding-right: 37px;
        background-position: 50px 8px;
        cursor: pointer;        
    }



    .conteiner_langs {
        width: 120px;
        height: 206px;
        position: absolute;
        background: no-repeat url("../img/svg/Check_lang_bg.svg");
        box-shadow: 0px 10px 35px rgba(0, 0, 0, 0.202502);
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        left: -20px;
        top: 60px;
        transition: all 0.3s linear;

        .langs_item {
            font-weight: 700;
            font-size: 28px;
            cursor: pointer;

            &:hover {
                color: #0072FF;
                transition: color 0.3s;
            }          
        }

        input[type="radio"]:checked+label{ display: none; } 
        input[type="radio"] {
        display: none;
        }
    }
}


.burger_menue {
  position: relative;
  z-index: 1;
  @include xs-block {
        top: 0;
        left: 25px;
}
@include xl-block {
    top: 0;
    left: 136px;
}

    input {
        display: block;
        width: 36px;
        height: 35px;
        position: absolute;
        top: -19px;
        left: -60px;
        cursor: pointer;
        opacity: 0; 
        z-index: 2;
        @include xs-block {
           position: absolute;
        }

        &:checked ~ span {
            position: fixed;
            top: 100px;
            right: 80px;
            z-index: 6;
            opacity: 1;
            transform: rotate(45deg) translate(-24px, -1px);
            background: #fff;
            @include xs-block {
                top: 80px;
                right: 30px;
            }
        }

        &:checked ~ span:nth-last-child(3) {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
        }

        &:checked ~ span:nth-last-child(2){
            transform: rotate(-45deg) translate(-16px, -6px);
       
        }

        &:checked ~ .burger_menu__links {
            visibility: visible;
            transform: translateX(0);
            transition: transform 0.9s cubic-bezier(0.07,0.2,0.05,1.0);
        }


    }

    span {
        display: block;
        width: 35px;
        height: 4px;
        right: 27px;
        margin-bottom: 10px;
        position: absolute; 
        top: -5px;
        background: #000;
        border-radius: 5px; 
        z-index: -1;
        transform-origin: 0px 0px;      
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s ease;

        &:nth-child(2) {
            transform: translateY(13px);
            transform-origin: 0% 100%;
            
        }

        &:nth-child(3) {
            transform-origin: 0% 100%;
          
        }
        
        &:nth-child(4) {
            transform: translateY(-13px);
            transform-origin: 0% 100%;
          
        }

 


        
      }

      .burger_menu__links {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        color: #fff;
        position: fixed;
        top: 0;
        right: 0;
        height: 100vh;
        margin: 0;
        visibility: visible;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), #0C1723;
        transform: translateX(100%);
        transition: transform 0.5s linear;
        padding-bottom: 50px;
        z-index: 4;

        @include xs-block {
            width: 100%;
        }

        @include lg-block {
            width: 50vw;
        }

        @include xl-block {
            width: 47vw;
        }

        a{
            padding: 15px;
            margin: 5px;
            color:white;
        }



        .lang_for_xs {
            @include lg(display, none);

            @include xs-block {
                align-self: flex-start;
                top: 70px;
                left: 150px;
                display: inline-block;
              
                .selected_lang {
                    color:white;
                    height: 30px;
                    font-weight: 700;
                    font-size: 22px;
                    background: no-repeat url("../img/svg/Arr_for_select _white.svg");
                    font-weight: 700;
                    font-size: 28px;
                    padding-right: 5px;
                    background-position: right 13px;
                } 
                .conteiner_langs {
                    left: -120px;
                }
                .langs_item {
                    color: #000;
                }
            }
        }

        .burger_menu__navigation_link {
            position: relative;
            left: 20%;

            @include xs-block {
                flex-shrink: 1;
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 50%;
                justify-content: space-around;
                top: 30px;
                left: 0;
            }

            @include lg-block {
                top: 60px;
                left: -55px;
                height: 55%;
            }

            @include xl-block {
                top: 83px;
                left: -150px;
                height: 400px;
            }



            li {
                text-align: center;
                margin: 19px 0;

                @include xs-block {
                    margin: 0;
                    &:nth-child(3) {
                        display: none;
                    }
                }
                @include lg-block {
                    &:nth-child(3) {
                        display: inherit;
                    }
                }

                a {
                    font-size: 22px;
                    font-weight: 500;
                    padding: 20px 20px;
                    @include lg-block {
                        font-weight: 400;
                        // margin: 25px 0;
                    }
                    &:hover {
                        color: #0072FF;
                        transition: color 0.3s;
                    }
                }
            }
        }
      

      .burger_menu__socials {
        height: 36px ;
        display: flex;
        justify-content: space-between;
        bottom: 18.5vh;
        left: 20%;
        position: relative;
        @include xs-block {
        bottom: 0;
        left: 0;
        width: 185px;

            img {
                height: 60px;
            }
            height: 60px;
        }

        @include lg-block {
            bottom: -105px;
            left: -55px;
            width: 132px;

            img {
                width: 36px;
                height: 36px;
            }
        }

        @include xl-block {
            bottom: -150px;
            width: 185px;
            left: -150px;
            width: 132px;

            img {
                width: 36px;
                height: 36px;
            }
        }

        
    
      }

      .burger_menu__foote_text {
          position: relative;
          bottom: 69px;
          font-size: 16px;
          font-weight: 300;

          @include xs-block {
              position: relative;
              bottom: 0;
              margin: 0 auto;
              a {
                padding: 0;
              }
            
          }
          @include lg-block {
            left: -55px;
          }

          @include xl-block {
            left: -150px;
        }
          a:hover {
              color: #0072FF;
              transition: color 0.3s;
          }
      }
    }
}

.header_xs_hidden {
    @include xs-block {
        display: none;
    }
    @include lg-block {
        display: flex;
    }
}

