#about {
    position: relative;
    @include lg-block {
        height: 100vh;
    }
}

.about { 
    position: relative;
    
    @include row-flex();

    @include xs-block() {
        height: max-content;
    }
    @include lg-block {
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
   

    .description {
        @include size-xs(13);
        @include size-lg(5);

        @include xs-block {
            position: relative;
            display: flex;
            flex-flow: nowrap column;
            justify-content: flex-start;
            align-items: center;
            height: 100%;
        }
        @include xl-block {
            position: static;
            align-items: flex-start;
        }

        .title {
            margin-bottom: 60px;
            @include xs-block {
                margin-top: -5px;
                margin-bottom: 39px;
            }

            @include sm-block {
                margin-bottom: 60px;
            }



        }

        .text {
            margin-top: 0;
            margin-bottom: 60px;
            @include xs-block {
                margin-bottom: 40px;
            }

            @include sm-block {
                font-size: 28px;
                margin-bottom: 60px;
            }
            @include lg-block {
                font-size: 24px;
            }

        }

        .downloads_icons {
            @include xs-block {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                height: 147px;
                align-self: flex-start; 
                margin-bottom: 20px; 
                img {
                    width: 200px;
                    height: 58px;
                }
            }

            @include sm-block {
                width: 100%;
                justify-content: space-around;
                margin-bottom: 0;
                height: 60px;
                img {
                    width: 220px;
                    height: 58;
                }
            }

            @include md-block {
                margin: 0 auto;
                width: 600px;
                justify-content: space-evenly;
                margin-bottom: 0;
                height: 60px;
                img {
                    width: auto;
                    height: 70;
                }
            }

            @include lg-block {
                width: 362px;
                height: 48px;
                justify-content: space-between;
                img {
                    width: auto;
                    height: 48px;
                }
            }

            @include xl-block {
                width: 442px;
                justify-content: space-between;
                margin: 0;
                img {
                    width: auto;
                    height: 58px;
                }
            }

        }
    }

    .viwe {
        height: 115vh;
        background: #0C1723;
        z-index: -1;
        border-bottom: none;
        border: 25px solid #EEEEEE;

        @include xs(display, none);
        @include size-md(12);
        @include size-lg (5);

        @include lg-block {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 400px;
            position: relative;
            top: -185px;
            left: -70px;
            border-radius: 68px;
        }
        @include xl-block {
            width: 540px;
            position: relative;
            left: 0;
            border-radius: 88px;
        }

        .top_of_viwe {
            border-bottom: 2px solid #242E39;     
            
            @include lg-block {
                margin: 0 20px;
                padding: 25px 0 30px 0;
            }

            @include xl-block {
                margin: 0 40px;
                padding: 37px 0 39px 0;
            }

            .head {
                position: relative;
                display: flex;
                justify-content: space-evenly;
                align-items: center;
                color: white;
                box-shadow: 0px -10px 28px rgba(0, 0, 0, 0.06);
                background: no-repeat url('../img/steve.png'), rgba(255, 255, 255, 0.05);

                @include lg-block {
                    width: 300px;
                    border-radius: 22px;
                    height: 90px;
                    background-position: 10px center;
                }

                @include xl-block {
                    width: 421px;
                    height: 100px;
                    border-radius: 28px;
                    background-position: 22px center;
                    // margin: 0 40px;
                }

                &:after {
                    content: "";
                    position: absolute;
                    bottom: 25px;
                    left: 80px;
                    background: url('../img/svg/stars.svg');
                    width: 105px;
                    height: 15px;

                    @include lg-block {
                        left: 77px;
                    }

                    @include xl-block {
                        left: 96px;
                    }
                }
                
                .steve_name {
                    line-height: 30px;
                    height: 53px;
                    border-right: 2px solid rgba(255, 255, 255, 0.1);

                    @include lg-block {
                        padding-left: 65px;
                        padding-right: 10px;
                        font-size: 16px;
                    }

                    @include xl-block {
                        font-size: 20px;
                        padding-left: 70px ;
                        padding-right: 30px;
                        // width: 260px;
                        // margin-right: 2px;
                       
                    }
                }

                .per {
                    text-transform: uppercase;
                    margin-bottom: 0px;

                    @include lg-block {
                        font-size: 16px;
                    }

                    @include xl-block {
                        font-size: 20px;
                    }

                    p {
                        color: #9297A2;
                        text-transform: none;
                        margin: 0;
                        margin-top: 10px;

                        @include lg-block {
                            font-size: 12px;
                            margin-left: 3px;
                        }
                        @include xl-block {
                            margin-left: 5px;
                            font-size: 14px;
                        }
                    }

                }
            }
        }

        .chat {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            color: white;
            font-weight: 300;
     

            @include lg-block {
                transform: translateY(352px);
                width: calc(100% - 40px);
                font-size: 14px;
                animation: move_chat_lg 20s 3s linear forwards;
            }
            @include xl-block {
                transform: translateY(402px);
                width: calc(100% - 59px);
                font-size: 16px;
                animation: move_chat 20s 3s linear forwards;
            }
            



            li {
                @include chat_message();
                animation: show_message 0.5s  linear forwards;
                opacity: 0;
                
                &:nth-child(1) {
                    animation-delay: 1.5s;
                }
                &:nth-child(2) {

                    animation-delay: 4s;
                }
                &:nth-child(3) {
 
                    animation-delay: 7s;
                }
                &:nth-child(4) {

                    animation-delay: 10s;
                }
                &:nth-child(5) {

                    animation-delay: 13s;
                }
                &:nth-child(6) {
                    animation-delay: 16s;
                }
            }   
        }
        .user {
            align-self: flex-end;
            background: #0072FF;
            display: inline-block;

            
               
        }
        
        .steve {
            background: rgba(255, 255, 255, 0.05);
            align-self: flex-start;
        }

        .steve_typing{
            @include typing();
            left: 40px;
            animation: show_typing_steve 20s   linear forwards;

            @include lg-block {
                top: 600px;
            }

            @include xl-block {
                top: 650px;
            }
        
        }

        .user_typing {
            @include typing();
            right: 40px;
            animation: show_typing_user 20s  linear forwards;
    
            @include lg-block {
                top: 600px;
            }

            @include xl-block {
                top: 650px;
            }

        }
    }

    .section_link {
        margin-bottom: 120px;
        @include sm-block {
            margin-bottom: 18%;
        }
        @include lg-block {
            position: absolute;
            bottom: 380px;
            display: flex;
            margin-top: 0;
            margin-bottom: 0;
            width: 100%;
        }
    }
}

#how_it_works {
    position: relative;
    background: #0C1723;
    @include sm-block {
        height: 1090px;
    }
    @include xs-block {
        height: 1196px;
    }
    @include lg-block {
        height: 101vh;
    }
}

.how_it_works {
    height: 100%;
    color: #fff;
    position: relative;

    .header_section {
        height: 15vh;
        
        @include xs-block {
            display: none;
        }
        @include lg-block {
            display: flex;
        }

    }

    .content {
        display: flex;

        @include xs-block {
            flex-direction: column;
            flex-wrap: wrap;
        }
        @include lg-block {
            flex-direction: row;
            justify-content: space-between;
        }
 

    .description {
        height: 85vh;

        @include description;
        @include size-xs(13);
        @include size-lg(6);

        @include xs-block {
          display: flex;
          flex-direction: column;
          align-items: center;
          height: min-content;
          position: static;       
        }
     
        .how_it_works__title {
            margin-bottom: 0;
            margin-top: 121px;
            @include sm-block{
                margin-top: 80px;
            }
            @include xl-block {
                margin-top: 0;
            }
        }

        .how_it_works__text {
            margin-bottom: 0;
            margin-top: 2.5vh;
        }
    }

    .viwe {
        box-sizing: border-box;
        position: relative;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 10vh;
        top: 0;

        @include xs-block {
            width: 100%;
            overflow: visible;
            left:0;
            border: none;
            padding-top:30px ;  
        }
        @include size-lg(5);
        @include lg-block {
            align-items: center;
            height: 94vh;
            border-radius: 0px 0px 68px 68px;
            border: 25px solid #19232F;
            width: 400px;
            border-top: none;
            overflow: hidden;
            left: -54px;
            top: -200px;
        }
        @include xl-block {
            width: 540px;
            border-radius: 0px 0px 80px 80px;
            left: 15px;
            top: -205px;
        }


        .viwe__title {
            font-weight: bold;
            border-bottom: solid 1px #242E39;

            @include xs-block {
                box-sizing: border-box;
                text-align: center;
                padding: 39px 70px;
                font-size: 28px;
                width: 315px;
            }
            @include sm-block {
                @include size-sm(12);
                font-size: 44px;
            }
            @include lg-block {
                text-align: center;
                padding: 20px 0 39px 0;
                font-size: 28px;
                width: 300px;
                margin: 22px 35px 0;
            }
            @include xl-block {
                text-align: center;
                padding: 39px 0;
                width: 420px;
                margin: 22px 35px 0;
            } 
        }

        .featches {
            padding-top: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 60px;

            @include xs-block {
                width: 315px;
            }

            @include size-sm(12);

            @include lg-block {
                width: 300px;
            }

            .feache__item {
                box-sizing: border-box;

                color: #000;
                margin-bottom: 4.9vh;
                position: relative;
                border-radius: 28px;

                @include xs-block {
                    width: 100%;
                    padding: 25px 0 25px 100px;
                    margin-bottom: 55px;
                }
                @include lg-block {
                    height: 100px;;
                    padding: 15px 0 25px 100px;
                    margin-bottom: 40px;
                    opacity: 0;
                }

                @include xl-block {
                    width: 420px;
                    padding: 25px 0 25px 100px;
                    margin-bottom: 40px;
                    opacity: 0;
                }
          
                &:nth-child(1){
                    background: no-repeat url("../img/ic_1.png"), #FFFFFF;
                    background-position: 25px center;
                    &::before {
                        content: "1";
                        @include feach_icon;
                    }
                }
                &:nth-child(2){
                    background: no-repeat url("../img/ic_2.png"), #FFFFFF;
                    background-position: 25px center;

                    &::before {
                        content: "2";
                        @include feach_icon;
                    }
                }
                &:nth-child(3){
                    background: no-repeat url("../img/ic_3.png"), #FFFFFF;
                    background-position: 25px center;

                    &::before {
                        content: "3";
                        @include feach_icon;
                    }
                }



                h4 {
                    font-size: 20px;
                }

                p {
                    margin: 10px 0 0 0;
                    font-size: 14px;
                    color: #9297A2;
                }
            }

        }
        }
    }

    .section_link {
        @include xs-block {
            width: 220px;
            position: absolute;
            bottom: -8%;
            left: calc(50% - 110px);
            padding-left: 0;
            background-position: 30px 5px;
        }
        @include sm-block {
            width: 220px;
            margin-top:0 ;
            bottom: -9.5%;
            left: calc(50% - 110px);
        }
        @include md-block {
           
            bottom: -10%;
            left: calc(50% - 120px);
        }
        @include lg-block {
            left: 0;
            bottom: 210px;
        }

        .how_it_works__selection_link {
            font-weight: bold;
            font-size: 28px;
            line-height: 40px;
            height: 40px;
            width: 220px;
            color: #fff;
            background: no-repeat url("../img/svg/Arr_for_link_white.svg");
            background-position: 10px 0px;
            background-size: 20px;
            padding: 0;

            @include xs-block {
                display: inline-block;
                width: 100%;
                background-position: 13px;
            }

        }
    }
}

